import React, { createContext, useContext, useEffect, useState } from "react";
import { Stitch, UserPasswordCredential } from 'mongodb-stitch-browser-sdk';

const AuthAppContext = createContext();

export const useAuthApp = () => {
    const app = useContext(AuthAppContext);
    if (!app) {
        throw new Error("Chame useAuthApp dentro de <AuthAppProvider />");
    }
    return app;
};

const AuthAppProvider = ({ appId, children }) => {
    // const [app, setApp] = useState(new Stitch.initializeAppClient(appId,));
    // useEffect(() => {
    //     if (!app) {
    //         setApp(new Stitch.initializeAppClient(appId,));
    //     }
    // }, [appId]);
    const app = new Stitch.initializeAppClient(appId,);
    const [currentUser, setCurrentUser] = useState(app.auth.user);
    async function logIn(credentials) {
        await app.auth.loginWithCredential(credentials)
        // .then(() => {
        //     // props.history.push("/page-1");
        // });
        setCurrentUser(app.auth.user)
    };
    async function logOut() {
        await app.logout();
        setCurrentUser(app.auth.user);
    };
    const wrapped = { ...app, currentUser, logIn, logOut };

    return (
        <AuthAppContext.Provider value={wrapped}>
            {children}
        </AuthAppContext.Provider>
    );
};

export default AuthAppProvider;